.price {
  background-color: #F2F2F2;
  text-transform: uppercase;

  &__item {
    background-color: #ffffff;
  }

  &__item:first-of-type {
    display: none;
  }

  &__item:last-of-type {
    display: none;
  }

  &__name {
    background-color: #283645;
  }

  &__name--hit {
   text-align: right;
  }

  &__name--packet {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    line-height: 30px;
    color: #ffffff;
  }

  &__name--usd {
    font-size: 18px;
    font-weight: 300;
    line-height: 30px;
    text-align: center;
    color: #ffffff;
  }

  &__description {
    font-size: 14px;
    font-weight: 400;
    line-height: 30px;
  }

  &__wrapper p {
    text-align: center;
  }

}
